.Holder_{
    margin-top: 150px;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
}
.AnimeCard{
    background-color: white;
    border: none;
    height: 375px;
    width: 250px;
    margin: 50px;
    box-shadow: 5px 5px 10px rgba(0,0,0,0.5);
    border-radius: 10px;
    transition: all 100ms linear;
}
.AnimeIMG{
    width: 100%;
    height: 80%;
    border-radius: 10px;
    margin-top: 5px;
    padding: 0;
}
.Title{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}
.AnimeCard:focus{
    box-shadow: 5px 5px 10px rgba(0,0,0,0.2);   
}
.AnimeCard:hover{
    box-shadow: 5px 5px 10px rgba(0,0,0,0.8);
}
.AnimeCard:active{
    transform: scale(96%);
    background-color: rgb(35,35,35);
    color: white;
}